.mc__title{text-align:center;color:#000;margin:20px auto 20px}.mc__form-container{width:100%;margin:0 auto;transition:height .2s ease}.mc__form{display:grid;grid-template-rows:-webkit-min-content -webkit-min-content;grid-template-rows:min-content min-content;justify-content:stretch;align-items:center;transition:height .2s ease}.mc__field-container{display:flex;align-items:center;justify-content:center}.mc__alert{text-align:center}.mc__alert--sending{color:#000;-webkit-animation:fade-loop .8s ease-in-out infinite alternate-reverse both;animation:fade-loop .8s ease-in-out infinite alternate-reverse both}.mc__alert--success{color:green;margin-top:-60px}.mc__alert--error{color:#ff0;font-weight:bold}.mc__alert--error a{color:gray;transition:.3s ease-in-out;font-weight:normal;display:block;margin-top:5px;opacity:.7}.mc__alert--error a:hover{text-decoration:none;opacity:1}@-webkit-keyframes fade-loop{0%{opacity:0}100%{opacity:1}}@keyframes fade-loop{0%{opacity:0}100%{opacity:1}}
.primaryBtn{background-color:#ffbb0b;border-style:none;font-family:"Poppins",-apple-system,BlinkMacSystemFont,avenir next,avenir,segoe ui,helvetica neue,helvetica,Ubuntu,roboto,noto,arial,sans-serif;font-weight:bold;color:#000;box-shadow:0 4px 23px rgba(0,0,0,.45);text-align:center;cursor:pointer;transition:.3s ease-in-out;-webkit-appearance:none;-webkit-border-radius:0;border:solid 1px #000;height:57px;border-top-right-radius:10px;border-bottom-right-radius:10px}.primaryBtn--normal{padding:8px 10px;font-size:16px}@media(min-width: 676px){.primaryBtn--normal{max-width:-webkit-max-content;max-width:max-content;padding:4px 18px}}.primaryBtn--big{padding:10px 15px;font-size:20px}@media(min-width: 676px){.primaryBtn--big{max-width:-webkit-max-content;max-width:max-content}}.primaryBtn:hover{box-shadow:0 1px 4px rgba(0,0,0,.45);-webkit-transform:scale(0.98);transform:scale(0.98);background-color:#a9a9a9;cursor:pointer}.primaryBtn--grid{display:grid;grid-template-columns:repeat(2, -webkit-max-content);grid-template-columns:repeat(2, max-content);grid-column-gap:10px;align-items:center;justify-content:center;text-decoration:none}
